.story {
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
    border-radius: 10px;
    margin: 10px 10px 10px 0;
    transition: transform 100ms ease-in;
    cursor: pointer;
}

.story > .story__avatar {
    position: absolute;
    border: 5px solid #2d88ff;
    margin: 5px;
    width: 33px;
    height: 33px;
}

.story > h4 {
    position: absolute;
    bottom: 10px;
    left: 10px;
    font-size: small;
    color: white;
}



.story:hover {
    transform: scale(1.07);
}