.sidebarRow {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

.sidebarRow > svg {
    font-size: xx-large;
    color: #2d88ff;
}

.sidebarRow > h4 {
    color: white;
    margin-left: 20px;
    font-weight: 600;
}


.sidebarRow:hover {
    background-color: #3a3b3c;
    border-radius: 10px;
}